@font-face {
    font-family: "Anta Regular";
    src: url("../public/fonts/Anta-Regular.ttf") format("truetype");
}

:root {
    --skip-btn-width: 50px;
}

.App {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    text-align: center;
    min-width: fit-content;
    max-width: 500px;
    height: fit-content;
    margin: auto;
    background: rgb(45, 45, 45);
    background: linear-gradient(315deg, rgba(45, 45, 45, 1) 14%, rgba(52, 52, 52, 1) 97%);
    color: rgb(255, 255, 255);
    padding: 2em;
    border-radius: 2em;
    box-shadow:
        0 4px 8px 0 rgba(0, 0, 0, 0.2),
        0 6px 20px 0 rgba(0, 0, 0, 0.19);
    opacity: 1;
}

h1,
h2,
h3 {
    font-family: "Anta Regular", "courier";
    font-weight: 700;
    text-shadow: 1px 1px 5px rgb(0, 0, 0);
}

hr {
    border: 0;
}

p {
    padding: 15px 15px;
    border-radius: 15px;
    background-color: #383838;
    margin: 3px;
    box-shadow:
        0 4px 8px 0 rgba(0, 0, 0, 0.04),
        0 6px 20px 0 rgba(0, 0, 0, 0.02);
}

input {
    width: auto;
    margin: auto clac(auto - var(--skip-btn-width)) auto auto;
    padding: 0.6em;
    font-size: large;
    border-radius: 0.9em;
    border: 3px solid #927d9b;
    background-color: white;
    color: black;
    font-weight: 700;
    box-shadow:
        0 4px 8px 0 rgba(0, 0, 0, 0.2),
        0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

#question {
    font-family: "Anta Regular", "courier";
    font-weight: 700;
    font-size: 2em;
    color: rgb(233, 233, 233);
    border: 2px solid rgb(51, 51, 51);
    border-radius: 15px;
    padding: 0.7em;
    width: 70%;
    min-width: 100px;
    max-width: 400px;
    min-height: 55px;
    height: fit-content;
    margin: 0.3em auto;
    background: rgb(34, 34, 34);
    text-shadow: 1px 1px 5px rgb(0, 0, 0);
}

#score {
    font-size: 1.1em;
    font-weight: 600;
    text-shadow: 1px 1px 5px rgb(0, 0, 0);
    background-color: #383838;
    margin: 0 20%;
    padding: 8px 10px;
    border-radius: 15px;
    box-shadow:
        0 4px 8px 0 rgba(12, 12, 12, 0.2),
        0 6px 20px 0 rgba(116, 116, 116, 0.1);
}

#timer {
    font-size: 1.1em;
    font-weight: 600;
    text-shadow: 1px 1px 5px rgb(0, 0, 0);
    background-color: #383838;
    margin: 5px 20%;
    padding: 8px 10px;
    border-radius: 15px;
    box-shadow:
        0 4px 8px 0 rgba(12, 12, 12, 0.2),
        0 6px 20px 0 rgba(116, 116, 116, 0.1);
}

#settingsBar {
    text-align: right;
}

#settingsBar button {
    background: none;
    border: none;
    color: white;
    cursor: pointer;
}

#settings {
    border-radius: 20px;
    margin-top: 5px;
    margin-bottom: 25px;
    min-width: inherit;
    max-width: 500px;
}

#start-game-btn,
#intro-confirm-btn {
    font-family: inherit;
    color: white;
    padding: 12px 19px;
    margin: 20px 10px 5px 10px;
    font-size: large;
    font-weight: 600;
    border-radius: 15px;
    background-color: #30a1a1;
    border: 0;
    cursor: pointer;
    box-shadow:
        0 4px 8px 0 rgba(0, 0, 0, 0.2),
        0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

#intro-div {
    text-align: left;
    font-size: small;
    max-width: 500px;
}

.intro-row:after {
    content: "";
    display: table;
    clear: both;
}

.intro-row {
    border-radius: 15px;
    background-color: #383838;
    margin: 5px 0;
    padding: 12px 17px 10px 0;
    box-shadow:
        0 4px 8px 0 rgba(0, 0, 0, 0.04),
        0 6px 20px 0 rgba(0, 0, 0, 0.02);
}

.intro-col-left {
    float: left;
    margin-top: 6px;
    padding-left: 10px;
    margin-left: 12px;
}

.intro-col-right {
    float: right;
    width: 80%;
    padding: 3px 0;
}

.input-correct-anim {
    animation:
        glow 0.2s ease-out 0s 2,
        shake 0.2s ease-in-out 0s 2;
}

.input-correct-no-anim {
    animation: glow ease-out 0.4s;
}

.new-question-anim {
    animation: growText 0.5s ease-in;
}

.score-correct-anim {
    animation: grow 0.3s ease-in;
    font-weight: 600;
}

.toggle-box {
    display: block;
    text-align: left;
    margin: 5px auto;
    padding: 0.6em;
    border: 1px solid rgb(56, 56, 56);
    border-radius: 15px;
    background-color: #383838;
    cursor: pointer;
    width: 220px;
}

.toggle-box label {
    font-weight: 600;
}

.toggle-box input {
    margin-right: 10px;
    padding: 5px;
}

.difficulty-select {
    display: block;
    border-radius: 15px;
    background-color: #383838;
    cursor: pointer;
    width: 220px;
    margin: 15px auto 0px auto;
    padding: 12px;
}

.difficulty-select input {
    max-width: 90%;
    margin-top: 20px;
    padding: 1px;
    height: 3px;
    border: 0;
    opacity: 0.9;
    -webkit-transition: 0.2s;
    transition: opacity 0.2s;
}

.hidden,
.hidden * {
    visibility: hidden !important;
    font-size: 0px !important;
    font-weight: 0 !important;
    max-height: 0px !important;
    padding: 0px !important;
    margin: 0px !important;
    border: 0 !important;
    border-radius: 0px !important;
}

.link {
    font-weight: 600;
    cursor: pointer;
}

#score-msg {
    font-weight: 700;
    font-size: large;
    margin: 20px;
    text-shadow: 1px 1px 5px rgb(0, 0, 0);
}

#score-board {
    padding: 10px;
    background-color: #242424;
    border-radius: 20px;
    margin: 10px auto;
    max-width: 400px;
}

.score-list-item {
    font-family: "Courier New", Courier, monospace;
    background-color: #383838;
    border-radius: 15px;
    padding: 7px;
    margin-top: 8px;
    color: rgb(204, 204, 204);
    font-size: small;
}

#congrats-msg {
    max-width: 400px;
    margin: auto;
    background-color: #242424;
    border-radius: 15px;
    padding: 18px 14px;
    border: 3px solid #363636;
    font-size: small;
    animation: growText 0.5s ease-in;
}

.fancy-heading {
    font-family: "Anta Regular", "courier";
    font-size: x-large;
    padding-top: 11px;
    padding-bottom: 11px;
    text-shadow: 1px 1px 5px rgb(0, 0, 0);
}

#privacy,
#mode-intro {
    font-size: smaller;
    border-radius: 16px;
    margin: 10px auto;
    width: auto;
    max-width: 500px;
}

.privacy-btn {
    font-family: inherit;
    font-weight: 700;
    margin: 10px 0;
    padding: 7px;
    cursor: pointer;
    border-radius: 20px;
    border: 4px solid #383838;
}

#deleted-msg {
    background-color: #30a1a1;
    font-weight: 700;
    color: azure;
    padding: 12px;
    border-radius: 15px;
    margin-bottom: 15px;
}

.start-link {
    cursor: pointer;
}

#game-menu p {
    width: auto;
}

.game-type-menu-item {
    padding: 15px;
    margin: 10px;
    font-size: large;
    border: 2px solid #242424;
    border-radius: 15px;
    cursor: pointer;
    background-color: #242424;
    box-shadow:
        0 4px 8px 0 rgba(0, 0, 0, 0.04),
        0 6px 20px 0 rgba(0, 0, 0, 0.02);
}

.game-type-item-desc {
    font-size: small;
    margin-top: 7px;
}

.loader {
    border: 8px solid #f3f3f3;
    border-top: 8px solid #3498db;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    margin: auto auto;
    animation: spin 2s linear infinite;
}

#answerbox {
    width: 290px;
    position: relative;
    margin: auto;
}

#answerbox input {
    width: 250px;
}

.skip-btn {
    position: absolute;
    right: 10px;
    top: 4px;
    z-index: 3;
    width: 50px;
    height: 41px;
    padding: 10px;
    border-radius: 15px;
    border: 0;
    color: white;
    font-weight: 700;
    cursor: pointer;
    opacity: 0.9;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes shake {
    0% {
        margin-left: 0rem;
    }

    25% {
        margin-left: 0.2rem;
    }

    75% {
        margin-left: -0.2rem;
    }

    100% {
        margin-left: 0rem;
    }
}

@keyframes glow {
    0% {
        box-shadow: 0em 0em 1em rgb(107, 196, 111);
    }

    25% {
        box-shadow: 0em 0em 1em rgb(107, 196, 111);
    }

    75% {
        box-shadow: 0em 0em 1em rgb(107, 196, 111);
    }

    100% {
        box-shadow: 0 0 0 rgb(238, 173, 235);
    }
}

@keyframes growText {
    from {
        transform: scale(0.4);
    }

    to {
        transform: scale(1);
    }
}

@keyframes grow {
    0% {
        font-size: 0.3em;
    }

    25% {
        font-size: 0.5em;
    }

    75% {
        font-size: 0.8em;
    }

    100% {
        font-size: 1.1em;
    }
}